import React, { useContext, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import AuthContext from "../context/AuthContext";

const links = [
    { to: "appls", name: "Заявления" },
    { to: "fis2gu", name: "ФИС" },
    { to: "abit2gu", name: "Abit" }
];

const originalTitle = document.title;

const Navigation: React.FC = () => {
    const { logout, name, vuz } = useContext(AuthContext);

    const { pathname } = useLocation();
    useEffect(() => {
        const path = (pathname === "/") ? "/appls" : pathname;
        document.title = links.filter(o => o.to === path.slice(1))[0].name;

        return () => { document.title = originalTitle; };
    }, [pathname]);

    const logoutHandler = (e: React.MouseEvent<any>) => {
        e.preventDefault();
        logout();
    };

    return (
        <nav>
            <span>
                {links.map(l => <Link
                    id={l.to}
                    key={l.to}
                    className="link"
                    to={"/" + l.to}
                >{l.name}</Link>)}
            </span>
            <span className="logout">
                <span>
                    <p>{vuz}</p>
                    <p>{name}</p>
                </span>
                <a id="logout" href="/" onClick={logoutHandler}>Выйти</a>
            </span>
        </nav>
    );
};

export default Navigation;