import { lazy, Suspense } from "react";
import { BrowserRouter, Navigate, Route, Routes } from "react-router-dom";
import Loader from "./components/Loader";
import Navigation from "./components/Navigation";
import useAuth from "./hooks/auth.hook";
import AuthContext from "./context/AuthContext";

const Itmo2GU = lazy(() => import("./pages/Abit2GU"));
const Fis2GU = lazy(() => import("./pages/Fis2GU"));
const CheckAppls = lazy(() => import("./pages/GetAppls"));
const AuthPage = lazy(() => import("./pages/AuthPage"));

const AuthApp: React.FC<boolean> = isAuth => {
    if (isAuth)
        return (
            <>
                <Navigation />
                <Suspense fallback={<Loader />}>
                    <Routes>
                        <Route path="/appls" element={<CheckAppls />} />
                        <Route path="/fis2gu" element={<Fis2GU />} />
                        <Route path="/abit2gu" element={<>
                            <Itmo2GU />
                            <Itmo2GU />
                        </>} />
                        <Route path="*" element={<Navigate to="/appls" replace />} />
                    </Routes>
                </Suspense>
            </>
        );

    return (
        <Suspense fallback={<Loader />}>
            <Routes>
                <Route element={<AuthPage />} path="/" />
                <Route path="*" element={<Navigate to="/" replace />} />
            </Routes>
        </Suspense>
    );
};

const App: React.FC = () => {
    const { name, ready, ...all } = useAuth();
    const isAuth = !!name;
    const routes = AuthApp(isAuth);

    if (!ready) {
        return <Loader />;
    }

    return (
        <AuthContext.Provider value={{ name, isAuth, ...all }}>
            <BrowserRouter>
                {routes}
            </BrowserRouter>
        </AuthContext.Provider>
    );
};

export default App;
