export function setCookies() {
    const temp = process.env.REACT_APP_COOKIE;
    if (temp) {
        temp.split(':').forEach(co => document.cookie = co);
    }
}

function genIsNumber(reg: RegExp, ...args: string[]) {
    return args.every(str => reg.test(str));
}

export const isNumber = {
    only: (...args: string[]) => genIsNumber(/^\d+$/, ...args),
    has: (...args: string[]) => genIsNumber(/\d/, ...args),
    non: (...args: string[]) => genIsNumber(/\D/, ...args)
};


export async function readToText(file: Blob): Promise<string> {
    const tmpFR = new FileReader();
    return new Promise((resolve, reject) => {
        tmpFR.onerror = () => {
            tmpFR.abort();
            reject(new Error("Problem parsing input file."));
        };
        tmpFR.onload = () => resolve(tmpFR.result as string);
        tmpFR.readAsText(file);
    });
}

export function checkStatus(req: Response) {
    const status = req.status;
    if (status === 200) {
        return req;
    } else {
        throw status;
    }
}

async function generalFetch<A>(url: string, method: "GET" | "POST", body: BodyInit | Object | null, type?: string) {
    const baseHeaders: HeadersInit = {
        "Cache-Control": "no-cache",
        "User-Agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/103.0.0.0 Safari/537.36",
        Host: "10.3.60.2",
        Pragma: "no-cache",
    };

    if (type) {
        baseHeaders["Content-Type"] = type;
    } else {
        baseHeaders.enctype = "multipart/form-data";
    }

    const config: RequestInit = {
        method,
        headers: baseHeaders,
        credentials: "include"
    };

    if (body) {
        config.body = (type) ? JSON.stringify(body) : body as BodyInit;
    }

    try {
        const res = await fetch(url, config);
        if (!res.ok) {
            throw new Error(res.statusText || "Что-то пошло не так");
        }
        const data: A = await res.json();
        //@ts-ignore
        if (!data?.done && data?.message === "Требуется авторизация.") {
            alert("Требуется повторная авторизация!");
            document.getElementById("logout")?.click();
        }
        return data;
    } catch (e: any) {
        throw e;
    }
}


export const f = {
    get: <A = IDefaulAns>(url: string, type = "application/json") => generalFetch<A>(url, "GET", null, type),
    post: <A = IDefaulAns>(url: string, body: Object, type = "application/json") => generalFetch<A>(url, "POST", body, type),
    postForm: <A = IDefaulAns>(url: string, body: BodyInit) => generalFetch<A>(url, "POST", body)
};


