export function formatIssuedDate(date: DS) {
    return date.toString().slice(0, 10);
}

export function makeForm(obj: Object) {
    // console.log(obj);
    let formData = new FormData();
    Object.entries(obj).forEach(([key, val]) => formData.append(key, val.toString()));
    return formData;
}

export function akt2json(json_data: any) {
    return URL.createObjectURL(new Blob([JSON.stringify(json_data)], { type: 'application/json' }));
}

interface postLoadFuncs {
    then: (str: string) => void;
    empty: () => void;
    post: () => void;
    setName?: (str: string) => void;
}

export function loadFiles(event: React.ChangeEvent<HTMLInputElement>, funcs: postLoadFuncs) {
    funcs.empty();
    const files = event.target.files;
    if (files?.length) {
        if (funcs.setName) {
            funcs.setName(files[0].name.slice(0, 4) + "_");
        }
        try {
            for (const file of files) {
                Promise.resolve(readToText(file)).then(funcs.then);
            }
            funcs.post();
        } catch (err) {
            console.log(err);
        }
    } else {
        console.log("empty");
        funcs.empty();
    }
}

export function spliceIntoChunks<T>(oldarr: T[], chunkSize: number) {
    const arr = [...oldarr];
    const res = [];
    while (arr.length > 0) {
        const chunk = arr.splice(0, chunkSize);
        res.push(chunk);
    }
    return res;
}

export function genArr(n: Number) {
    return Array(n).fill(0).map((_, i) => i + 1);
}

export function log2csv(log: Object) {
    const csv = Object.entries(log)
        .map(([key, val]) => [key, ...Object.values(val)].join(";")).join("\n");
    return URL.createObjectURL(new Blob([csv], { type: 'text/csv;charset=utf-8' }));
}

export function genISODate(withTZO = false) {
    const pad = (num: number) => (num < 10 ? '0' : '') + num;
    const date = new Date();
    const tzo = -date.getTimezoneOffset();
    const dif = tzo >= 0 ? '+' : '-';
    const tzoString = (withTZO) ?
        dif + pad(Math.floor(Math.abs(tzo) / 60)) +
        ':' + pad(Math.abs(tzo) % 60) : "";

    return date.getFullYear() +
        '-' + pad(date.getMonth() + 1) +
        '-' + pad(date.getDate()) +
        'T' + pad(date.getHours()) +
        ':' + pad(date.getMinutes()) +
        ':' + pad(date.getSeconds()) +
        tzoString;
}

export function genUpdateFunc(prefix: string, convert: boolean, resize: number, isFull = false) {
    const [func, ext] = (convert) ? [log2csv, "csv"] : [akt2json, "json"];
    return function (c: number, log: any, isEnd = false): [string, string] {
        return [`${prefix}${isFull ? "" : "Full"}${resize === 1 ? "" : "Fast"}${isEnd ? genISODate() : ""}_${c}.${ext}`, func(log)];
    };
}

export function addLog<T>(logObj: T, key: keyof T, str: string & T[keyof T]) {
    logObj[key] = str;
    console.log(str);
}

export async function getIdBySnilsOrFIO(logObj: IBaseLog, snils?: string) {

    const res = await f.get<IEntrantsSearch>("/api/entrants/list?page=1&limit=5" +
        (snils) ? `&search_snils=${snils}` :
        "&search_surname=" + encodeURIComponent(logObj.surname) +
        "&search_name=" + encodeURIComponent(logObj.name) +
        "&search_patronymic=" + encodeURIComponent(logObj.patronymic));

    if (res.done) {
        if (res.paginator.total === 1) {
            const person = res.data[0];
            if (person?.id > -1) {
                return { sspvo_id: person.id, snils: person.snils };
            }
        } else if (res.paginator.total === 0) {
            addLog(logObj, "error_gu_search", "Не существует на ГУ!");
        } else if (res.paginator.total > 1) {
            addLog(logObj, "error_gu_search", "Больше 1 человека с таким ФИО");
        }
    }

    return { sspvo_id: null, snils: null };
}