import { useCallback, useEffect, useState } from "react";
import { IDefaulAns } from "../models/sspvo";
import { f } from "../ts/utils";

interface LoginData {
    name: string;
    vuz: string;
}

const storageName = "SSPVO";

const useAuth = () => {
    const [vuz, setVuz] = useState("");
    const [name, setName] = useState("");
    const [ready, setReady] = useState(false);

    const login = useCallback((adminName: string, adminVuz: string) => {
        setName(adminName);
        setVuz(adminVuz);
        localStorage.setItem(storageName, JSON.stringify({ name: adminName, vuz: adminVuz }));
    }, []);

    const logout = useCallback(async () => {
        setName("");
        setVuz("");
        localStorage.removeItem(storageName);
        await f.get<IDefaulAns>("/api/logout");
    }, []);

    useEffect(() => {
        const data: LoginData = JSON.parse(localStorage.getItem(storageName)!);
        if (data?.name?.length > 0) {
            login(data.name, data.vuz);
        }
        setReady(true);
    }, [login]);

    return { login, logout, name, vuz, ready };
};

export default useAuth;